import React from 'react'
import Lottie from 'react-lottie';
import wealthCreator from 'assets/lottie/wealthCreatorAnimation.json'
import shortTerm from 'assets/lottie/shortTermAnimation.json'
import stock from 'assets/lottie/stockAnimation.json'
import goal from "assets/lottie/Goal.json"
// import goal from "assets/lottie/animation_500_l3ygpb5j.gif"

export function FinancialGoalAnimation({ style }) {

    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: goal,
        rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice'
        }
    };

    return (
        <div>
            <Lottie options={defaultOptions}
                height={80}
                width={80}
                style={style}
            />
        </div>
        // <img src={goal} height={80}
        //     width={80} />
    )
}

